import React from "react";

type Props = {
  text?: string;
};
const RubberText: React.FC<Props> = ({ text = "" }) => {
  return (
    <div className="flex flex-row  animate-bounceInLeft">
      {text.split("").map((c, i) => (
        <h1
          key={i}
          className="font-virgil text-7xl hover:animate-rubber hover:text-lime-400 cursor-pointer"
        >
          {c}
        </h1>
      ))}
    </div>
  );
};

export default RubberText;
